import React from 'react';
import Logo from './img/start.png'
import StartMenuLogo from './img/win-95-logo.png'
import ShutDown from './img/shut-down.png'
import './Start.scss'

class Start extends React.Component {

  constructor(props) {
    super(props);
    this.state = {"showHideSidenav":"hidden", "startButton":"initial"};
  }

  toggleSidenav() {
      let showSide = (this.state.showHideSidenav === "hidden") ? "show" : "hidden"
      let buttonEffect = (this.state.startButton === "initial") ? "active" : "initial";
      this.setState({"showHideSidenav":showSide, "startButton":buttonEffect});

  }

  render() {

    let startMenuIcons = [
      {name:'Shut down...', icon:ShutDown},
      {name:'Suspend', icon:ShutDown}
    ];

    return (
      <div>
      <div className={`StartMenu-container ${this.state.showHideSidenav}`}>
        <div className="brand-bar">
          <img className="start-menu-logo" src={StartMenuLogo} alt="Start Menu Logo" />
        </div>
        <div className="contents-bar">
          {startMenuIcons.map((icons, index) => {
            return <div className="list-item" key={index}>
              <img className="start-menu-icons" src={icons.icon} alt={index} />
              <span>{icons.name}</span>
            </div>})}
        </div>
      </div>
        <div className={`start-container ${this.state.startButton}`} onClick={this.toggleSidenav.bind(this)}>
          <div className="logo-container">
            <img className="logo" src={Logo} alt="Logo" />
          </div>
          <div className="text-container">
            <p>Start</p>
          </div>
        </div>
      </div>
    );
  }
}



export default Start;
