import React from 'react';
import MyComputer from './img/my-computer.png';
import MyDocuments from './img/my-documents.png';
import RecycleBin from './img/recycle-bin.png';
import MyNetworkPlaces from './img/my-network-places.png';
import './Icon.scss';


class Icon extends React.Component {

  render() {

    return (
      <div className="icon">
        <div className="icon-container" >
          <img src={this.props.icon} alt={this.props.name} className="icon"></img>
          <p className="name">{this.props.name}</p>
        </div>
      </div>
    );
  }
}

export default Icon;
