import React from 'react';
import Icon from './Icon'
import Window from '../Window/Window'
import './Desktop.scss';
import MyComputer from './img/my-computer.png';
import MyDocuments from './img/my-documents.png';
import RecycleBin from './img/recycle-bin.png';
import MyNetworkPlaces from './img/my-network-places.png';

class Desktop extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      desktopIcons: [
        {name:'My Computer', icon:MyComputer},
        {name:'My Documents', icon:MyDocuments},
        {name:'Recycle Bin', icon:RecycleBin},
        {name:'My Network Places', icon:MyNetworkPlaces},
      ]
    }
    this.close = React.createRef();
  }

  render() {

    return (
      <div className="desktop-container">
        {this.state.desktopIcons.map( (item, i) => <Icon key={i} name={ item.name } icon = {item.icon} className = {i} />)}
        {this.state.desktopIcons.map( (item, i) => <Window key={i} name={ item.name } size={i} ref={this.close}/>)}
      </div>
    );
  }
}

export default Desktop;
